html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
menu,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
main,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
  display: block;
}

*[hidden] {
  display: none;
}

body {
  line-height: 1;
}

menu,
ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html,
body {
  -moz-font-feature-settings: "kern=1,liga=1,calt=1";
  font-feature-settings: "kern" 1,"liga" 1,"calt" 1;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  width: 100%;
  color: #475669;
  line-height: 18px;
  min-height: 100%;
  min-width: 576px;
  font-size: 13px;
  font-family: "Roboto",Helvetica,sans-serif;
}

html a,
body a {
  color: #f4645f;
  outline: none;
  text-decoration: none;
}

html a:active,
html a:hover,
body a:active,
body a:hover {
  color: #e45b56;
  background: none;
  text-decoration: underline;
}

html a.dashed,
body a.dashed {
  text-decoration: none;
  border-bottom: 1px dashed transparent;
}

html a.dashed:active,
html a.dashed:hover,
body a.dashed:active,
body a.dashed:hover {
  text-decoration: none;
  border-bottom: 1px dashed #e45b56;
}

html pre,
html code,
body pre,
body code {
  font-size: 13px;
  font-family: "Roboto Mono",Menlo,Monaco,Consolas,"Courier New",monospace;
}

html code,
body code {
  background: #eff2f7;
  border-radius: 2px;
  padding: 2px 6px;
  box-sizing: border-box;
  line-height: 18px;
  height: 18px;
  color: inherit;
}

html pre>code,
body pre>code {
  display: block;
  white-space: pre;
  background: #f9fafc;
  padding: 10px 15px;
  box-shadow: inset 3px 0 0 #f4645f;
  height: auto;
  border-radius: 0 2px 2px 0;
  line-height: 19px;
}

html ul li,
body ul li {
  list-style: square;
  margin: 4px 0;
}

html hr,
body hr {
  border: none;
  margin: 10px 0;
  display: block;
  height: 1px;
  background: #e5e9f2;
}

html h1,
html h2,
html h3,
html h4,
html h5,
html h6,
body h1,
body h2,
body h3,
body h4,
body h5,
body h6 {
  font-family: "Roboto",Helvetica,sans-serif;
  font-weight: 400;
  line-height: 1.4;
  padding: 0 5px;
  display: block;
  width: 100%;
  box-sizing: border-box;
}

html h1.underline,
html h2.underline,
html h3.underline,
html h4.underline,
html h5.underline,
html h6.underline,
body h1.underline,
body h2.underline,
body h3.underline,
body h4.underline,
body h5.underline,
body h6.underline {
  box-shadow: 0 1px 0 #e5e9f2;
}

html h1 .anchor,
html h2 .anchor,
html h3 .anchor,
html h4 .anchor,
html h5 .anchor,
html h6 .anchor,
body h1 .anchor,
body h2 .anchor,
body h3 .anchor,
body h4 .anchor,
body h5 .anchor,
body h6 .anchor {
  opacity: .3;
  font-weight: 400;
  font-size: .9em;
  text-decoration: none !important;
}

html h1 .anchor:before,
html h2 .anchor:before,
html h3 .anchor:before,
html h4 .anchor:before,
html h5 .anchor:before,
html h6 .anchor:before,
body h1 .anchor:before,
body h2 .anchor:before,
body h3 .anchor:before,
body h4 .anchor:before,
body h5 .anchor:before,
body h6 .anchor:before {
  content: "#";
}

html h1 .anchor:focus,
html h1 .anchor:active,
html h2 .anchor:focus,
html h2 .anchor:active,
html h3 .anchor:focus,
html h3 .anchor:active,
html h4 .anchor:focus,
html h4 .anchor:active,
html h5 .anchor:focus,
html h5 .anchor:active,
html h6 .anchor:focus,
html h6 .anchor:active,
body h1 .anchor:focus,
body h1 .anchor:active,
body h2 .anchor:focus,
body h2 .anchor:active,
body h3 .anchor:focus,
body h3 .anchor:active,
body h4 .anchor:focus,
body h4 .anchor:active,
body h5 .anchor:focus,
body h5 .anchor:active,
body h6 .anchor:focus,
body h6 .anchor:active {
  opacity: .7;
}

html h1,
body h1 {
  font-size: 4.236rem;
  font-weight: 100;
  margin: 4.236rem 0 2.118rem 0;
}

html h2,
body h2 {
  font-size: 2.618rem;
  margin: 2.618rem 0 1.309rem 0;
}

html h3,
body h3 {
  font-size: 1.618rem;
  font-weight: 400;
  margin: 1.618rem 0 .809rem 0;
}

html h4,
body h4 {
  font-size: 14px;
  text-transform: uppercase;
  margin: 14px 0 7px 0;
}

html h5,
html h6,
body h5,
body h6 {
  font-size: 13px;
  font-weight: bold;
  margin: 14px 0 7px 0;
}

html table,
body table {
  width: 100%;
  border-radius: 3px;
  border-spacing: 0;
  border-collapse: collapse;
  box-shadow: 0 0 0 1px #e5e9f2;
}

html table+table,
body table+table {
  margin-top: 10px;
}

html table tr,
body table tr {
  margin: 0;
  padding: 0;
}

html table tr>td,
body table tr>td {
  padding: 10px;
}

html table tr>td:first-child,
body table tr>td:first-child {
  padding-left: 15px;
}

html table tr>td:last-child,
body table tr>td:last-child {
  padding-right: 15px;
}

html table tr:first-child>td,
body table tr:first-child>td {
  box-shadow: none;
}

html table tr:first-child>td:first-child,
body table tr:first-child>td:first-child {
  border-top-left-radius: 3px;
}

html table tr:first-child>td:last-child,
body table tr:first-child>td:last-child {
  border-top-right-radius: 3px;
}

html table tr:last-child>td:first-child,
body table tr:last-child>td:first-child {
  border-bottom-left-radius: 3px;
}

html table tr:last-child>td:last-child,
body table tr:last-child>td:last-child {
  border-bottom-right-radius: 3px;
}

html table tbody tr>td,
body table tbody tr>td {
  box-shadow: inset 0 1px 0 #e5e9f2;
}

html table tbody tr:first-child>td,
body table tbody tr:first-child>td {
  box-shadow: none;
}

html table tbody tr:nth-child(2n)>td,
body table tbody tr:nth-child(2n)>td {
  background: #f9fafc;
}

html table thead tr>td,
body table thead tr>td {
  background: #f9fafc;
  box-shadow: inset 0 -1px 0 #e5e9f2,inset 1px 0 0 #e5e9f2 !important;
  font-weight: bold;
}

html table thead tr>td:first-child,
body table thead tr>td:first-child {
  box-shadow: inset 0 -1px 0 #e5e9f2 !important;
}

html table thead tr:last-child>td:first-child,
html table thead tr:last-child>td:last-child,
body table thead tr:last-child>td:first-child,
body table thead tr:last-child>td:last-child {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.header {
  background: #fff;
  position: relative;
  padding-top: 9px;
}

.header:before {
  content: "";
  width: 100%;
  height: 9px;
  background: url(/images/header_line.png) center center repeat-x;
  display: block;
  position: absolute;
  top: 0;
  box-shadow: inset 0 -1px 2px rgba(31,45,61,.3);
}

.header>.extras {
  height: 32px;
  line-height: 32px;
  width: 100%;
  border-bottom: #e5e9f2 1px solid;
}

.header>.extras .extras-items {
  max-width: 1200px;
  height: inherit;
  justify-content: space-between;
  margin: 0 auto;
  display: flex;
}

.header>.extras a {
  color: #475669;
  margin: 0 10px;
}

.header>.extras a:hover {
  color: #324057;
}

@media (max-width: 767px) {
  .header>.extras {
    display: none;
  }
}

.header>.menu {
  padding: 15px 0;
  height: 42px;
  border-bottom: #e5e9f2 1px solid;
}

.header>.menu .menu-content {
  max-width: 1200px;
  display: flex;
  height: inherit;
  justify-content: stretch;
  margin: 0 auto;
}

.header>.menu .logo {
  display: block;
  text-decoration: none;
  width: 150px;
  height: 100%;
  background: url(/images/logo.png) center center no-repeat;
  margin-right: 50px;
  flex: 150px;
  max-width: 150px;
  min-width: 150px;
}

.header>.menu .logo h1 {
  display: none;
}

.header>.menu .menu-items {
  width: 100%;
  line-height: 42px;
  flex: 1;
  height: inherit;
  display: flex;
  justify-content: space-between;
  font-size: 14px;
}

.header>.menu .menu-items>span,
.header>.menu .menu-items>a {
  color: #475669;
  height: 32px;
  margin: 0 10px;
  font-weight: bold;
  text-decoration: none;
  position: relative;
}

.header>.menu .menu-items>span {
  color: #99a9bf;
}

.header>.menu .menu-items>a {
  transition: color .3s ease;
}

.header>.menu .menu-items>a:before {
  pointer-events: none;
  content: "";
  position: absolute;
  bottom: 0;
  height: 2px;
  width: 100%;
  background: #f4645f;
  transform: scaleX(0);
  transform-origin: 100% 100%;
  transition: transform .3s ease;
}

.header>.menu .menu-items>a:hover {
  color: #f4645f;
}

.header>.menu .menu-items>a:hover:before {
  transform: scaleX(1);
  transform-origin: 0 0;
}

