.header {
    background: #fff;
    position: relative;
    padding-top: 9px;

    &:before {
        content: '';
        width: 100%;
        height: 9px;
        background: url(/images/header_line.png) center center repeat-x;
        display: block;
        position: absolute;
        top: 0;
        box-shadow: inset 0 -1px 2px rgba($color-black, .3);
    }

    & > .extras {
        height: 32px;
        line-height: 32px;
        width: 100%;
        border-bottom: $color-border 1px solid;

        .extras-items {
            max-width: map-get($grid-breakpoints, xl);
            height: inherit;
            justify-content: space-between;
            margin: 0 auto;
            display: flex;
        }

        a {
            color: $color-text;
            margin: 0 10px;

            &:hover {
                color: $color-light-black;
            }
        }

        @include media-breakpoint-down(sm) {
            display: none;
        }
    }

    & > .menu {
        padding: 15px 0;
        height: 42px;
        border-bottom: $color-border 1px solid;

        .menu-content {
            max-width: map-get($grid-breakpoints, xl);
            display: flex;
            height: inherit;
            justify-content: stretch;
            margin: 0 auto;
        }

        .logo {
            display: block;
            text-decoration: none;
            width: 150px;
            height: 100%;
            background: url(/images/logo.png) center center no-repeat;
            margin-right: 50px;
            flex: 150px;
            max-width: 150px;
            min-width: 150px;

            h1 {
                display: none;
            }
        }

        .menu-items {
            width: 100%;
            line-height: 42px;
            flex: 1;
            height: inherit;
            display: flex;
            justify-content: space-between;
            font-size: 14px;

            & > span, & > a {
                color: $color-text;
                height: 32px;
                margin: 0 10px;
                font-weight: bold;
                text-decoration: none;
                position: relative;
            }

            & > span {
                color: $color-description;
            }

            & > a {
                transition: color .3s ease;

                &:before {
                    pointer-events: none;
                    content: '';
                    position: absolute;
                    bottom: 0;
                    height: 2px;
                    width: 100%;
                    background: $color-main;
                    transform: scaleX(0);
                    transform-origin: 100% 100%;
                    transition: transform .3s ease;
                }

                &:hover {
                    color: $color-main;

                    &:before {
                        transform: scaleX(1);
                        transform-origin: 0 0;
                    }
                }
            }
        }
    }
}
